*, :before, :after {
  box-sizing: inherit;
}

html * {
  scroll-behavior: smooth;
}

html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video {
  font: inherit;
  vertical-align: baseline;
  border: 0;
  margin: 0;
  padding: 0;
}

i {
  font-style: italic;
}

body, html {
  box-sizing: border-box;
  min-width: 100vw;
  min-height: 100vh;
  text-rendering: optimizelegibility;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  margin: 0;
  font-size: 62.5%;
  line-height: 1;
}

main, footer, header, hgroup, menu, nav {
  display: block;
}

ol, ul, li, a {
  text-decoration: none;
  list-style-type: none;
}

a {
  color: inherit;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

section {
  flex-direction: column;
  display: flex;
}

section, div, ul {
  position: relative;
}

object {
  pointer-events: none;
}

:root {
  --space-small: 5%;
  --space-medium: 10%;
  --space-large: 18%;
  --gap-small: 1rem;
  --gap-medium: 2rem;
  --gap-large: 4rem;
  --gap-huge: 8rem;
  --white: #fff;
  --silk: #f5f5f5;
  --silk-shade: #f5f5f580;
  --charcol: #424c4c;
  --charcol-shade: #424c4c85;
  --black: #1c1c1c;
  --icon: 5rem;
  --border-radius: 10px;
  --animation-time: .2s;
  --animation-time-long: 6s;
}

@keyframes pulse {
  0% {
    box-shadow: 0 0 0 0 var(--charcol-shade);
  }
}

.container, .music, .card, .timeline {
  width: 90%;
}

@media (width >= 992px) {
  .container, .music, .card, .timeline {
    max-width: 600px;
  }
}

@media (width >= 768px) {
  .container, .music, .card, .timeline {
    width: 100%;
    max-width: 750px;
  }
}

@media (width >= 1200px) {
  .container, .music, .card, .timeline {
    max-width: 950px;
  }
}

.section, .section__top {
  justify-content: center;
  align-items: center;
  padding: 60px 0;
  display: flex;
}

@media (width >= 768px) {
  .section__top {
    padding: 200px 0 60px;
  }
}

.outlined {
  border: 2px solid var(--charcol-shade);
  transition: border var(--animation-time) ease;
}

.outlined:hover {
  border: 2px solid #fff0;
}

.rounded, .timeline__button, .burger, .nav__list {
  border-radius: 10px;
}

.rounded__large {
  border-radius: 20px;
}

.acrylic, .card__item__content, .timeline__item, .burger, .nav__menu, .nav__list, .hero__content {
  background-blend-mode: exclusion;
  backdrop-filter: blur(24px);
}

.fullframe, .fullframe--hover {
  object-fit: cover;
  width: 100%;
  height: 100%;
  position: absolute;
}

.fullframe--hover {
  transition: transform var(--animation-time);
  transform: scale(1);
}

.fullframe--hover:hover {
  transform: scale(1.1);
}

.button, .card__item__link, .timeline__button {
  background: var(--button-color);
  transition: background var(--animation-time), box-shadow var(--animation-time);
  display: block;
}

.box-shadow, .music__item, .card__item__link, .card__item, .timeline__button, .timeline__item, .nav__list, .hero__content {
  box-shadow: 0 0 2px var(--shadow-color);
}

.hover-pulse:hover, .social__link:hover, .hover-pulse:focus, .social__link:focus {
  animation: .5s pulse;
  box-shadow: 0 0 0 36px #0000;
}

.hover-fill {
  --hover-x: 50%;
  --hover-y: 50%;
  position: relative;
  overflow: hidden;
}

.hover-fill:after {
  content: "";
  top: var(--hover-y);
  left: var(--hover-x);
  background-color: var(--highlight-color);
  width: 0;
  height: 0;
  transition: width var(--animation-time) ease-in-out, height var(--animation-time) ease-in-out;
  z-index: -1;
  border-radius: 50%;
  position: absolute;
  transform: translate(-50%, -50%);
}

.hover-fill:hover:after {
  width: 300%;
  height: 300%;
}

.hover-fill:active {
  background-color: var(--highlight-color);
}

.hover-shadow {
  transition: all var(--animation-time);
}

.hover-shadow:after {
  content: " ";
  opacity: 0;
  width: 100%;
  height: 100%;
  box-shadow: 0 5px 15px var(--accent-color);
  transition: opacity var(--animation-time) ease;
}

.hover-shadow:hover:after {
  opacity: 1;
}

.hover-line {
  box-shadow: inset 0 -2px 0 0 var(--highlight-color);
  transition: all var(--animation-time);
  box-sizing: border-box;
  padding: 0 3px;
  text-decoration: none;
  display: inline-block;
}

.hover-line:hover {
  box-shadow: inset 0 -24px 0 0 var(--highlight-color);
  border: none;
}

.faded {
  opacity: .7;
}

