:root, html[data-theme="light"] {
  --background-color: #eee;
  --text-color: #000;
  --border-color: #c7cdd1;
  --button-color: #fff;
  --accent-color: #ffffff78;
  --highlight-color: #efd096;
  --line-color: linear-gradient(to top, #0000 0%, #00000030 2.5%, #00000030 97.5%, #0000 100%);
  --shadow-color: #00000030;
}

html[data-theme="dark"] {
  --background-color: #1c1c1c;
  --text-color: #f5f5f5;
  --border-color: #323232;
  --button-color: #474747;
  --accent-color: #2c2c2c78;
  --highlight-color: #397a95;
  --line-color: linear-gradient(to top, #fff0 0%, #ffffff7e 2.5%, #ffffff7e 97.5%, #fff0 100%);
  --shadow-color: #ffffff7e;
}

html, body {
  background: var(--background-color);
  color: var(--text-color);
  overflow-x: hidden;
}

.themeswitch {
  z-index: 9;
  position: absolute;
  top: 25px;
  right: 25px;
}

.themeswitch__checkbox {
  width: 50px;
  height: 24px;
  display: inline-block;
  position: relative;
}

.themeswitch__checkbox input {
  display: none;
}

.icon-sun, .icon-moon {
  mix-blend-mode: difference;
  display: none;
}

html[data-theme="light"] .icon-sun, html[data-theme="dark"] .icon-moon {
  display: inline-block;
}

.slider {
  cursor: pointer;
  background: var(--highlight-color);
  transition: var(--animation-time);
  border-radius: 34px;
  position: absolute;
  inset: 0;
}

.slider:before {
  content: "";
  width: 18px;
  height: 18px;
  transition: var(--animation-time);
  background: #fff;
  border-radius: 50%;
  position: absolute;
  bottom: 3px;
  left: 3px;
}

input:checked + .slider:before {
  transform: translateX(26px);
}

.hero {
  min-height: 600px;
  display: flex;
}

@media (width >= 768px) {
  .hero {
    flex-direction: row;
    height: 100vh;
  }
}

.hero__background {
  min-height: 550px;
  position: relative;
}

@media (width >= 768px) {
  .hero__background {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
  }
}

.hero__content {
  padding: var(--gap-medium);
  background: var(--accent-color);
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

.hero__content h1, .hero__content p {
  width: 90%;
  max-width: 450px;
}

@media (width >= 768px) {
  .hero__content h1, .hero__content p {
    width: 100%;
    max-width: 400px;
  }

  .hero__content {
    padding: var(--gap-large);
    border-radius: 20px;
    position: absolute;
    top: 40%;
    right: 10%;
  }
}

.hero__title {
  text-align: left;
  margin-bottom: var(--gap-medium);
  line-height: 1.5;
}

.background {
  object-fit: cover;
  z-index: -1;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  overflow: hidden;
}

.filter--blur {
  filter: blur(48px) opacity(.25);
}

.filter--darken {
  filter: brightness(.8);
}

.book {
  grid-template-columns: 1fr 1fr;
  display: grid;
}

@media (width >= 768px) {
  .book {
    grid-template-columns: 1fr 1fr 1fr 1fr;
  }
}

.book__cover {
  max-height: 400px;
}

.book__item {
  margin: var(--gap-small);
  text-align: center;
  flex-direction: column;
  justify-content: space-between;
  display: flex;
}

.book__title {
  font-size: 2rem;
}

.note {
  grid-template-columns: 1fr;
  display: grid;
}

@media (width >= 768px) {
  .note {
    grid-template-columns: 1fr 1fr;
  }
}

.note__item {
  margin: var(--gap-small);
  position: relative;
  overflow: hidden;
}

.note__figure {
  width: 100%;
  height: 300px;
}

.note__title {
  margin: var(--space-small);
  padding: var(--gap-small) var(--gap-medium);
  background: var(--accent-color);
  position: absolute;
  bottom: 0;
  right: 0;
}

.nav {
  display: block;
}

.nav__list {
  z-index: 9;
  background: var(--accent-color);
  align-items: center;
  display: none;
  position: absolute;
  top: 150px;
  left: 50%;
  overflow: hidden;
  transform: translate(-50%, -50%);
}

@media (width >= 768px) {
  .nav__list {
    display: flex;
  }
}

.nav__list > li {
  text-align: center;
  display: block;
  position: relative;
}

.nav__list > li > a {
  min-width: 150px;
  padding: var(--gap-small) var(--gap-medium);
  font-weight: 500;
  display: inline-block;
}

.nav__list > li:not(:last-child) {
  box-shadow: inset -10px 0 10px -15px var(--shadow-color);
}

.nav__rect {
  background: var(--highlight-color);
  width: 150px;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  transform: translateX(0);
}

.nav__menu {
  visibility: hidden;
  opacity: 0;
  z-index: 8;
  background: var(--accent-color);
  width: 100%;
  transition: var(--animation-time);
  flex-flow: wrap;
  padding: 60px 0;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  overflow-x: hidden;
  transform: translateY(-100%);
}

@media (width >= 768px) {
  .nav__menu {
    display: none;
  }
}

.nav__menu > li {
  width: 100%;
  padding: var(--gap-small) 0;
  justify-content: center;
  display: flex;
}

.nav__menu > li > a {
  text-align: center;
  min-width: 120px;
  font-size: 26px;
  font-weight: 500;
  line-height: 2;
}

.nav__menu--visible {
  visibility: visible;
  opacity: 1;
  transform: translateY(0);
}

.burger {
  z-index: 9;
  justify-content: center;
  align-items: center;
  width: 30px;
  height: 30px;
  padding: 20px;
  display: flex;
  position: fixed;
  top: 15px;
  left: 20px;
}

@media (width >= 768px) {
  .burger {
    display: none;
  }
}

.burger__inner {
  background: var(--text-color);
  width: 80%;
  height: 3px;
  transition: var(--animation-time);
  position: absolute;
  left: 10%;
}

.burger__inner:before {
  content: "";
  background: var(--text-color);
  width: 80%;
  height: 3px;
  transition: var(--animation-time);
  position: absolute;
  top: -10px;
  left: 10%;
}

.burger__inner:after {
  content: " ";
  background: var(--text-color);
  width: 80%;
  height: 3px;
  transition: var(--animation-time);
  position: absolute;
  top: 10px;
  left: 10%;
}

.burger__inner--active {
  background-color: #0000;
}

.burger__inner--active:before {
  top: 0;
  transform: rotate(45deg);
}

.burger__inner--active:after {
  top: 0;
  transform: rotate(-45deg);
}

.footer {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 60px 0;
  display: flex;
}

.social {
  margin: var(--gap-medium) 0;
  flex-direction: row;
  display: flex;
}

.social__link {
  height: var(--icon);
  width: var(--icon);
  border-radius: 10px;
  display: inline-block;
}

.social__link svg {
  width: 100%;
  height: 100%;
  fill: var(--text-color);
}

.social__item {
  margin: 0 var(--gap-small);
  display: block;
}

.icon {
  color: var(--text-color);
  text-align: center;
  z-index: 1;
  width: 50%;
  font-size: 32px;
  line-height: 60px;
}

.dynamic-background {
  animation: var(--animation-time-long) movement linear infinite;
  background-image: radial-gradient(closest-side, #9e5041, #a74f3b00), radial-gradient(closest-side, #894372, #a33b8000), radial-gradient(closest-side, #857741, #8d834c00), radial-gradient(closest-side, #533e9a, #5536a400), radial-gradient(closest-side, #94683c, #965e3b00), radial-gradient(closest-side, #9a553e, #bb5d3d00);
  background-position: -80vmax -80vmax, 60vmax -30vmax, 10vmax 10vmax, -30vmax -10vmax, 50vmax 50vmax, -20vmax, -40vmax;
  background-repeat: no-repeat;
  background-size: 130vmax 130vmax, 80vmax 80vmax, 90vmax 90vmax, 110vmax 110vmax, 90vmax 90vmax, 100vmax, 100vmax;
}

@keyframes movement {
  0%, 100% {
    background-position: -80vmax -80vmax, 60vmax -30vmax, 10vmax 10vmax, -30vmax -10vmax, 50vmax 50vmax;
    background-size: 130vmax 130vmax, 80vmax 80vmax, 90vmax 90vmax, 110vmax 110vmax, 90vmax 90vmax;
  }

  25% {
    background-position: -60vmax -90vmax, 50vmax -40vmax, 0 -20vmax, -40vmax -20vmax, 40vmax 60vmax;
    background-size: 100vmax 100vmax, 90vmax 90vmax, 100vmax 100vmax, 90vmax 90vmax, 60vmax 60vmax;
  }

  50% {
    background-position: -50vmax -70vmax, 40vmax -30vmax, 10vmax 0, 20vmax 10vmax, 30vmax 70vmax;
    background-size: 80vmax 80vmax, 110vmax 110vmax, 80vmax 80vmax, 60vmax 60vmax, 80vmax 80vmax;
  }

  75% {
    background-position: -50vmax -40vmax, 50vmax -30vmax, 20vmax 0, -10vmax 10vmax, 40vmax 60vmax;
    background-size: 90vmax 90vmax, 90vmax 90vmax, 100vmax 100vmax, 90vmax 90vmax, 70vmax 70vmax;
  }
}

.timeline {
  flex-direction: column;
  display: flex;
}

.timeline__line {
  background: var(--line-color);
  border: none;
  width: 2px;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 12px;
}

.timeline > h2 {
  margin: 40px 0 24px;
  font-weight: 600;
}

.timeline__item {
  background: var(--accent-color);
  border-radius: 20px;
  flex-direction: column;
  display: flex;
}

@media (width >= 768px) {
  .timeline__item {
    margin-left: 36px;
  }
}

.timeline__content {
  padding: 24px;
}

.timeline__content > h3 {
  margin: 0 0 12px;
  font-weight: 500;
  line-height: 1.15;
}

.timeline__content:not(:last-child) {
  box-shadow: inset 0 -5px 10px -10px var(--shadow-color);
}

.timeline__button {
  padding: var(--gap-small) var(--gap-large);
  z-index: 1;
  margin-top: 48px;
  font-weight: 500;
}

.card {
  flex-direction: column;
  display: flex;
}

.card__item {
  width: 100%;
  transition: var(--animation-time) ease;
  border-radius: 20px;
  flex-direction: column;
  justify-content: space-between;
  margin: 20px 0;
  display: flex;
  position: relative;
  overflow: hidden;
}

@media (width >= 1200px) {
  .card__item {
    flex-direction: row;
  }
}

.card__item:nth-of-type(n) {
  background: linear-gradient(112deg, #05c0591e, #f9fd501e);
}

.card__item:nth-of-type(2n) {
  background: linear-gradient(72deg, #6bbdcf1e, #268ea81e);
}

.card__item:nth-of-type(3n) {
  background: linear-gradient(90deg, #b559921e, #d79cb11e);
}

.card__item__figure {
  min-width: 450px;
  min-height: 350px;
  max-height: 500px;
  position: relative;
}

.card__item__figure img {
  width: 100%;
  height: 110%;
  position: absolute;
  top: 0;
  left: 0;
}

@media (width >= 1200px) {
  .card__item__figure {
    width: 450px;
    max-height: 550px;
  }

  .card__item__figure img {
    width: 110%;
    height: 100%;
  }
}

.card__item__content {
  padding: var(--gap-medium);
  background: var(--accent-color);
  flex-direction: column;
  display: flex;
  position: relative;
}

@media (width >= 1200px) {
  .card__item__content {
    padding: var(--gap-large);
  }
}

.card__item__content > h3 {
  margin-bottom: var(--gap-small);
  font-size: 26px;
  line-height: 1.15;
}

.card__item__content > p:first-of-type {
  margin-top: 0;
}

.card__item__link {
  min-width: 250px;
  margin-top: var(--gap-small);
  padding: var(--gap-small) var(--gap-medium);
